import React, { memo, useState } from 'react'
import PropTypes from 'prop-types'

import { Button, Text, TextField, createStyles, utils } from '@instacart/cocktail'

import { SectionTitle } from '@instacart/enterprise-ui-common'

const useStyles = createStyles(
  tokens => ({
    root: {
      display: 'inline-block',
      width: '100%',
      marginBottom: tokens.size.spacing.small,
      [utils.mediaQuery.minWidth(tokens.breakpoint.medium)]: {
        width: 300,
        marginRight: tokens.size.spacing.xxSmall,
      },
    },
    input: {
      width: '100%',
      [utils.mediaQuery.minWidth(tokens.breakpoint.medium)]: {
        width: 300,
      },
    },
    text: {
      display: 'block',
      margin: utils.toPx`0 0 ${tokens.size.spacing.small} 0`,
    },
    button: {
      width: '100%',
      verticalAlign: 'top',
      [utils.mediaQuery.minWidth(tokens.breakpoint.medium)]: {
        width: 120,
      },
    },
  }),
  { displayName: 'SubscriptionReedem' }
)

const defaultLang = {
  sectionRedeemTitle: 'Redeem a Subscription',
  enterCodePlaceholder: 'Enter subscription code',
  enterCode: 'Enter a subscription code',
  errorMsg: 'The code provided is invalid',
  submit: 'Submit',
}

const defaultFormData = {
  subscriptionCode: '',
}

const SubscriptionRedeem = props => {
  const styles = useStyles(props)
  const {
    i18n,
    loading,
    initialSubscriptionCode,
    onSubscriptionCodeSubmit,
    onRedeemSubscriptionSuccess,
  } = props
  const [formData, setFormData] = useState(defaultFormData)
  const [error, setError] = useState(false)
  const [prevInitialSubscriptionCode, setPrevInitialSubscriptionCode] = useState(undefined)

  const lang = { ...defaultLang, ...i18n }
  const inputName = 'subscriptionCode'
  const inputID = `${inputName}-input`

  // Pre-populate input field only once, the first time only.
  if (!prevInitialSubscriptionCode && initialSubscriptionCode) {
    setPrevInitialSubscriptionCode(initialSubscriptionCode)
    setFormData({ ...formData, [inputName]: initialSubscriptionCode })
  }

  const handleInputChange = name => event => {
    setFormData({ ...formData, [name]: event.target.value })
    setError(false)
  }

  const redeemSubscription = () =>
    onSubscriptionCodeSubmit(formData.subscriptionCode)
      .then(onRedeemSubscriptionSuccess)
      .catch(() => setError(true))

  const handleKeyDown = evt => {
    switch (evt.key) {
      case 'Enter':
        redeemSubscription()
        break
      default:
    }
  }

  return (
    <div>
      <SectionTitle text={lang.sectionRedeemTitle} />
      <label htmlFor={inputID} css={styles.text}>
        <Text component="span" variant="body">
          {lang.enterCode}
        </Text>
      </label>
      <TextField
        id={inputID}
        css={styles.input}
        FormControlProps={{ css: styles.root }}
        inputSize="small"
        placeholder={lang.enterCodePlaceholder}
        value={formData.subscriptionCode}
        onChange={handleInputChange(inputName)}
        name={inputName}
        disabled={loading}
        error={error}
        helpText={error && lang.errorMsg}
        onKeyDown={handleKeyDown}
        data-test="submit-code-input"
        required
      />
      <Button
        loading={loading}
        css={styles.button}
        style={{ verticalAlign: styles.button.verticalAlign }}
        size="small"
        onClick={redeemSubscription}
        data-test="submit-code-btn"
      >
        {lang.submit}
      </Button>
    </div>
  )
}

SubscriptionRedeem.defaultProps = {
  i18n: {},
  loading: false,
}

SubscriptionRedeem.propTypes = {
  /**
   * i18n language object
   */
  i18n: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.shape({
      sectionRedeemTitle: PropTypes.string,
      enterCodePlaceholder: PropTypes.string,
      enterCode: PropTypes.string,
      errorMsg: PropTypes.string,
      submit: PropTypes.string,
    }),
  ]),
  /**
   * Whether this component is in loading state
   */
  loading: PropTypes.bool,
  /**
   * The function that submits the subscription code
   */
  onSubscriptionCodeSubmit: PropTypes.func.isRequired,
  /**
   * This can be passed in to pre-populate the subscription code input field
   */
  initialSubscriptionCode: PropTypes.string,
  /**
   * Function that triggers when redeeming a subscription is successful
   */
  onRedeemSubscriptionSuccess: PropTypes.func,
}

export default memo(SubscriptionRedeem)
