import React from 'react'
import { Alert, utils, useMediaQuery } from '@instacart/cocktail'
import { config as configService } from '@instacart/enterprise-services'
import { QueryConfigProvider } from '@instacart/enterprise-services-hooks'
import { ErrorBoundary, SectionTitle } from '@instacart/enterprise-ui-common'

import TransactionTable from './components/TransactionTable'
import TransactionAccordions from './components/TransactionAccordions'

const defaultLang = {
  transactionHistory: 'Transaction History',
  transactionHistoryError: 'Unable to get transaction data',
}

const LoyaltyTransactions = () => {
  const lang = { ...defaultLang, ...configService.selectors.getLang() }
  // Suspense disabled for manual control over loading
  const queryConfig = { suspense: false }

  const isMobile = useMediaQuery(tokens => utils.mediaQuery.maxWidth(tokens.breakpoint.medium))

  return (
    <>
      {!isMobile && (
        <SectionTitle text={lang.transactionHistory} data-test="transaction-section-heading" />
      )}
      <QueryConfigProvider config={queryConfig}>
        <ErrorBoundary
          fallback={
            <Alert variant="error" data-test="transaction-error-fallback">
              {lang.transactionHistoryError}
            </Alert>
          }
        >
          {isMobile ? <TransactionAccordions /> : <TransactionTable />}
        </ErrorBoundary>
      </QueryConfigProvider>
    </>
  )
}

export default LoyaltyTransactions
