import React, { memo } from 'react'
import PropTypes from 'prop-types'
import {
  Text,
  Accordion,
  AccordionTrigger,
  AccordionContent,
  AccordionChevron,
  ListItemContent,
  ListItemAdornment,
  createStyles,
} from '@instacart/cocktail'

const useStyles = createStyles(
  {
    details: {
      display: 'flex',
      justifyContent: 'space-between',
    },
  },
  { displayName: 'TransactionAccordion' }
)

const TransactionAccordion = ({ transaction, transactionFields, fieldFormatters }) => {
  const styles = useStyles()

  const formatValue = (value, fieldType) =>
    (fieldType && fieldFormatters[fieldType](value)) || value || '-'

  return (
    <Accordion>
      <AccordionTrigger
        contentId={`${transaction.id}-details`}
        data-test="transaction-accordion-trigger"
      >
        <ListItemContent>
          <Text variant="body" bold data-test="transaction-transaction_time">
            {formatValue(
              transaction.transaction_time,
              transactionFields.find(field => field.name === 'transaction_time')?.type
            )}
          </Text>
          <Text variant="body" data-test="transaction-location">
            {formatValue(
              transaction.location,
              transactionFields.find(field => field.name === 'location')?.type
            )}
          </Text>
        </ListItemContent>
        <ListItemAdornment>
          <Text variant="body" data-test="transaction-amount">
            {formatValue(
              transaction.amount,
              transactionFields.find(field => field.name === 'amount')?.type
            )}
          </Text>
        </ListItemAdornment>
        <ListItemAdornment>
          <AccordionChevron />
        </ListItemAdornment>
      </AccordionTrigger>
      <AccordionContent id={`${transaction.id}-details`} card>
        {transactionFields.map(field => {
          const value = transaction[field.name]

          return (
            <div key={`${transaction.id}-${field.name}`} css={styles.details}>
              <Text variant="body" bold>
                {field.label}
              </Text>
              <Text variant="body" data-test={`transaction-details-${field.name}`}>
                {formatValue(value, field.type)}
              </Text>
            </div>
          )
        })}
      </AccordionContent>
    </Accordion>
  )
}

TransactionAccordion.propTypes = {
  /**
   * Transaction object
   *  Must include id.
   *  transaction_time, location, and amount are displayed on the
   *  accordion trigger, and all other fields within the accordion
   *  content.
   * */
  transaction: PropTypes.shape({
    id: PropTypes.string.isRequired,
    transaction_time: PropTypes.string,
    location: PropTypes.string,
    amount: PropTypes.string,
  }).isRequired,
  /** Array of all fields the retailer has configured to show for a transaction */
  transactionFields: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      type: PropTypes.string,
    })
  ),
  /**
   * Formatting/mutator functions to apply to the transaction field values
   * Should match with the type prop on the transactionFields object
   * */
  // eslint-disable-next-line react/forbid-prop-types
  fieldFormatters: PropTypes.object,
}

TransactionAccordion.defaultValues = {
  fieldFormatters: {},
}

export default memo(TransactionAccordion)
