import { loyalty as loyaltyService } from '@instacart/enterprise-services'
import { useLoyaltyCardsQuery } from '@instacart/enterprise-services-hooks'

export default config => {
  const loyaltyCardsResponse = useLoyaltyCardsQuery(config)

  return {
    ...loyaltyCardsResponse,
    getPrimaryLoyaltyCard: loyaltyService.selectors.getPrimaryLoyaltyCard,
  }
}
