import React, { memo } from 'react'
import PropTypes from 'prop-types'
import { config as configService } from '@instacart/enterprise-services'
import { createStyles, utils, Text, Button } from '@instacart/cocktail'
import InstacartLogo from '../assets/images/ic-logo.svg'

const useStyles = createStyles(
  tokens => ({
    root: {
      padding: tokens.size.spacing.small,
      background: tokens.color.neutral.grey1,
      borderRadius: tokens.border.radius.regular,
    },
    heading: { marginTop: 0 },
    footer: {
      display: 'flex',
      flexFlow: 'row wrap',
      justifyContent: 'space-between',
      alignItems: 'flex-end',
      [utils.mediaQuery.maxWidth(tokens.breakpoint.small)]: {
        justifyContent: 'flex-end',
      },
    },
    button: {
      [utils.mediaQuery.maxWidth(tokens.breakpoint.small)]: {
        width: '100%',
      },
    },
    logo: {
      [utils.mediaQuery.maxWidth(tokens.breakpoint.small)]: {
        marginTop: tokens.size.spacing.small,
      },
    },
  }),
  { displayName: 'AccountImportNotice' }
)

const AccountImportNotice = ({ onImportClick, ...restProps }) => {
  const styles = useStyles()
  const lang = configService.selectors.getLang()

  return (
    <div css={styles.root} {...restProps}>
      <Text css={styles.heading} component="h2" variant="body" bold>
        {lang.icImportAccount.title}
      </Text>
      <Text component="p" variant="caption">
        {lang.icImportAccount.intro}
      </Text>
      <Text component="p" variant="caption">
        {lang.icImportAccount.listTitle}
      </Text>
      <ul>
        {lang.icImportAccount.featureList.map((feature, index) => (
          // eslint-disable-next-line react/no-array-index-key
          <li key={index}>
            <Text variant="caption">{feature}</Text>
          </li>
        ))}
      </ul>
      <div css={styles.footer}>
        <Button css={styles.button} onClick={onImportClick} data-test="import-account-button">
          {lang.icImportAccount.importAccount}
        </Button>
        <img src={InstacartLogo} css={styles.logo} alt="Instacart Logo" width="86px" />
      </div>
    </div>
  )
}

AccountImportNotice.propTypes = {
  onImportClick: PropTypes.func.isRequired,
}

export default memo(AccountImportNotice)
