import React, { memo } from 'react'
import {
  createStyles,
  GridContainer,
  GridItem,
  Input,
  FormControl,
  FormLabel,
  Radio,
  Text,
} from '@instacart/cocktail'
import { config as configService } from '@instacart/enterprise-services'
import { SectionTitle } from '@instacart/enterprise-ui-common'

import { UPDATE_FIELD } from '../constants'
import { useFormDataContext } from '../utils/formDataContext'

const useStyles = createStyles(tokens => ({
  fieldset: {
    border: 0,
    padding: 0,
    margin: 0,
    width: '100%',
    marginBottom: tokens.size.layout.xSmall,
    '> legend': {
      marginBottom: tokens.size.layout.xSmall,
      padding: 0,
    },
  },
  hidden: {
    display: 'none',
  },
}))

// Converts MM/dd/yyyy into yyyy-MM-dd
const importBirthday = birthday => {
  // Ensure `birthday` exists and is valid (need regex check for Safari)
  if (!birthday) return ''
  if (!birthday.match(/(0?[1-9]|1[012])[\/\-](0?[1-9]|[12][0-9]|3[01])[\/\-]\d{4}/g)) {
    return birthday
  }
  const [MM, dd, yyyy] = birthday.split('/')
  return `${yyyy}-${MM}-${dd}`
}

// Converts yyyy-MM-dd into MM/dd/yyyy
const exportBirthday = birthday => {
  // Ensure `birthday` exists and is valid (need regex check for Safari)
  if (!birthday) return null
  if (!birthday.match(/[12]\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[12]\d|3[01])/g)) return birthday
  const [yyyy, MM, dd] = birthday.split('-')
  return `${MM}/${dd}/${yyyy}`
}

const PersonalInformation = () => {
  const styles = useStyles()

  // TODO: Subscribe to config store rather than using the selector directly.
  // Requires buiilding a redux store subscribe hook for easy consumption.
  const config = configService.selectors.getConfig()
  const lang = configService.selectors.getLang()

  const [{ personalInformation, inputRefs }, dispatch] = useFormDataContext()

  const dispatchChangeEvent = (field, data) => {
    dispatch({
      type: UPDATE_FIELD,
      section: 'personalInformation',
      field,
      data,
    })
  }
  const handleUpdate = event => {
    dispatchChangeEvent(event.target.name, event.target.value)
  }

  const handleBirthdayUpdate = event => {
    dispatchChangeEvent(event.target.name, exportBirthday(event.target.value))
  }

  return (
    <div data-test="personal-information">
      <SectionTitle text={lang.personalInformation} />
      {config.profilePersonalDetails.showBirthday && (
        <GridContainer align="baseline" spacing="xxxSmall">
          <GridItem xSmall={12} small={2}>
            <FormLabel htmlFor="birthday">{lang.birthday}</FormLabel>
          </GridItem>
          <GridItem xSmall={12} small={10}>
            <FormControl>
              <Input
                data-test="birthday"
                id="birthday"
                ref={inputRefs?.personalInformation?.birthdayRef}
                type="date"
                size="small"
                name="birthday"
                placeholder="yyyy-mm-dd"
                onChange={handleBirthdayUpdate}
                style={{ fontFamily: 'inherit' }}
                value={importBirthday(personalInformation.birthday)}
              />
            </FormControl>
          </GridItem>
        </GridContainer>
      )}
      {config.profilePersonalDetails.showGender && (
        <fieldset css={styles.fieldset}>
          <legend css={styles.hidden}>{lang.gender}</legend>
          <GridContainer align="center" spacing="xxxSmall">
            <GridItem xSmall={12} small={2}>
              <FormLabel>{lang.gender}</FormLabel>
            </GridItem>
            <GridItem xSmall={12} small={10}>
              <Radio
                data-test="gender-male"
                value="male"
                name="gender"
                id="genderMale"
                onChange={handleUpdate}
                checked={personalInformation.gender === 'male'}
              >
                <Text>{lang.male}</Text>
              </Radio>
              <Radio
                data-test="gender-female"
                name="gender"
                value="female"
                id="genderFemale"
                onChange={handleUpdate}
                checked={personalInformation.gender === 'female'}
              >
                <Text>{lang.female}</Text>
              </Radio>
            </GridItem>
          </GridContainer>
        </fieldset>
      )}
    </div>
  )
}

PersonalInformation.propTypes = {}

export default memo(PersonalInformation)
