import React from 'react'
import PropTypes from 'prop-types'
import { v4 as uuid } from 'uuid'
import dayjs from 'dayjs'
import { config as configService, lang as langService } from '@instacart/enterprise-services'
import {
  Alert,
  Pagination,
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
  Text,
  LoadingDots,
} from '@instacart/cocktail'
import usePaginatedTransactionHistory from '../hooks/usePaginatedTransactionHistory'
import TransactionRow from './TransactionRow'

const DEFAULT_LIMIT = 20

const defaultLang = {
  noTransactions: 'There are currently no transactions to show.',
  goToPage: 'Go to page {{page}}',
}

const fieldFormatters = {
  date: date => dayjs(date).format('MMM DD, YYYY - hh:mm A'),
}

const TransactionTable = ({ limitPerPage }) => {
  const {
    currentPage,
    setCurrentPage,
    totalCount,
    loyaltyTransactionFields,
    data,
    isLoading,
  } = usePaginatedTransactionHistory(limitPerPage)

  const lang = { ...defaultLang, ...configService.selectors.getLang() }

  if (typeof totalCount !== 'number') {
    return <LoadingDots data-test="loading-unknown-total" />
  }

  if (totalCount === 0) {
    return (
      <Alert variant="info" data-test="alert-no-transactions">
        {lang.noTransactions}
      </Alert>
    )
  }

  return (
    <>
      <Table>
        <TableHead>
          <TableRow>
            {loyaltyTransactionFields.map(field => (
              <TableCell
                key={`heading-${field.name}`}
                heading
                scope="col"
                data-test={`heading-${field.name}`}
              >
                <Text bold>{field.label}</Text>
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {isLoading ? (
            <TableRow>
              <TableCell colSpan={loyaltyTransactionFields.length} align="center">
                <LoadingDots data-test="loading-known-total" />
              </TableCell>
            </TableRow>
          ) : (
            data.transactions.map((transaction, index) => {
              const transactionId = uuid()
              return (
                <TransactionRow
                  key={transactionId}
                  transaction={{ ...transaction, id: transactionId }}
                  index={index}
                  transactionFields={loyaltyTransactionFields}
                  fieldFormatters={fieldFormatters}
                />
              )
            })
          )}
        </TableBody>
      </Table>
      <Pagination
        currentPage={currentPage}
        pageCount={Math.ceil(totalCount / limitPerPage)}
        onPageClick={page => setCurrentPage(page)}
        ariaLabelLinkBuilder={page => langService.interpolate(lang.goToPage, { page })}
      />
    </>
  )
}

TransactionTable.propTypes = {
  /** Maximum number of transactions to show per page */
  limitPerPage: PropTypes.number,
}

TransactionTable.defaultProps = {
  limitPerPage: DEFAULT_LIMIT,
}

export default TransactionTable
