import React, { memo } from 'react'
import PropTypes from 'prop-types'
import { createStyles, utils, Alert, Button } from '@instacart/cocktail'
import { useConfig, useLang } from '@app/services/config/hooks'
import { AccountImportNotice, AccountLinkedNotice } from '@instacart/enterprise-account-import'
import BillingAddress from './BillingAddress'
import ContactAddress from './ContactAddress'
import OtherInformation from './OtherInformation'
import ContactInformation from './ContactInformation'
import PersonalInformation from './PersonalInformation'
import ConnectedSocialAccounts from './ConnectedSocialAccounts'
import DeployInformation from './DeployInformation'
import { useFormDataContext } from '../utils/formDataContext'

// TODO: Make alerts dismissable

const useStyles = createStyles(tokens => ({
  flexContainer: {
    [utils.mediaQuery.minWidth(tokens.breakpoint.medium)]: {
      display: 'flex',
      alignItems: 'flex-start',
    },
  },
  flexForm: {
    [utils.mediaQuery.minWidth(tokens.breakpoint.medium)]: {
      flexGrow: 1,
      flexBasis: '55%',
    },
  },
  alert: {
    marginTop: tokens.size.spacing.large,
  },
  deployInfo: {
    marginTop: tokens.size.spacing.large,
  },
  saveButton: {
    marginTop: tokens.size.spacing.large,
  },
  accountImport: {
    [utils.mediaQuery.minWidth(tokens.breakpoint.medium)]: {
      order: 1,
      flexGrow: 1,
      flexBasis: '45%',
      maxWidth: '450px',
      marginTop: tokens.size.spacing.large,
      marginLeft: tokens.size.spacing.large,
    },
  },
}))

const AccountProfile = ({
  isSaving,
  onSubmit,
  alert,
  onAlertClose,
  disconnectSocial,
  importIcAccount,
}) => {
  const styles = useStyles()

  const config = useConfig()
  const lang = useLang()

  const [
    {
      otherInformation: { icImported, icUserId },
    },
  ] = useFormDataContext()

  // In an environment with partial user isolation, users that
  // have been isolated will have an IC User ID
  const isIcImportEnabled = config.icImportAccount && icUserId

  return (
    <div css={[config.icImportAccount && styles.flexContainer]}>
      {isIcImportEnabled && (
        <div css={styles.accountImport}>
          {icImported ? (
            <AccountLinkedNotice data-test="account-linked-notice" />
          ) : (
            <AccountImportNotice
              data-test="account-import-notice"
              onImportClick={importIcAccount}
            />
          )}
        </div>
      )}
      <form
        data-test="account-profile-form"
        css={[config.icImportAccount && styles.flexForm]}
        onSubmit={onSubmit}
      >
        <ContactInformation />

        {config.profilePersonalDetails.shouldShow && <PersonalInformation />}

        {config.profileAddress.shouldShow && <ContactAddress />}

        {config.enableBillingInformation && <BillingAddress />}

        {config.customProfileFields && config.customProfileFields.length > 0 && (
          <OtherInformation />
        )}

        {config.socialSignOn?.enabled && (
          <ConnectedSocialAccounts disconnectSocial={disconnectSocial} />
        )}

        {!config.profile.disableSave && (
          <Button
            data-test="submit-button"
            type="submit"
            loading={isSaving}
            css={styles.saveButton}
          >
            {lang.saveChanges}
          </Button>
        )}

        {alert && (
          <div css={styles.alert} data-test="alert">
            <Alert variant={alert.type} onClick={onAlertClose}>
              {alert.message}
            </Alert>
          </div>
        )}

        {config.env.mode === 'dev' && (
          <div css={styles.deployInfo}>
            <DeployInformation />
          </div>
        )}
      </form>
    </div>
  )
}

AccountProfile.propTypes = {
  isSaving: PropTypes.bool,
  disconnectSocial: PropTypes.func.isRequired,
  onSubmit: PropTypes.func,
  onAlertClose: PropTypes.func,
  alert: PropTypes.shape({
    type: PropTypes.string,
    message: PropTypes.node,
  }),
  importIcAccount: PropTypes.func,
}

export default memo(AccountProfile)
