import React from 'react'

import { config, loading, subscriptions } from '@instacart/enterprise-services'

import ServiceProvider from '../services/ServiceProvider'
import SubscriptionPage from './SubscriptionPage'

class App extends React.PureComponent {
  render() {
    return (
      <ServiceProvider services={[config, subscriptions, loading]}>
        <SubscriptionPage {...this.props} />
      </ServiceProvider>
    )
  }
}

export default App
