import React, { memo } from 'react'
import PropTypes from 'prop-types'
import {
  utils,
  createStyles,
  Text,
  Button,
  ModalContainer,
  ModalHeader,
  ModalHeaderText,
  ModalContent,
  Alert,
} from '@instacart/cocktail'
import { config as configService } from '@instacart/enterprise-services'

const useStyles = createStyles(
  tokens => ({
    heading: {
      margin: 0,
      [utils.mediaQuery.minWidth(tokens.breakpoint.small)]: {
        fontSize: utils.toPx`${tokens.size.font.h3}`,
      },
    },
    footer: {
      marginTop: tokens.size.spacing.large,
    },
  }),
  { displayName: 'AccountImportErrorModal' }
)

const AccountImportErrorModal = ({ goToAccount, close }) => {
  const styles = useStyles()
  const lang = configService.selectors.getLang()

  return (
    <ModalContainer>
      <ModalHeader>
        <ModalHeaderText>
          <Text css={styles.heading} component="h1" variant="large" color="inverse">
            {lang.icImportAccount.title}
          </Text>
        </ModalHeaderText>
      </ModalHeader>
      <ModalContent layout="dialog">
        <Alert variant="error">{lang.icImportAccount.error.failed}</Alert>
        <Text component="p" variant="body">
          {lang.icImportAccount.error.tryAgain}
        </Text>
        <div css={styles.footer}>
          <Button fullWidth onClick={goToAccount} data-test="go-to-account-button">
            {lang.icImportAccount.error.goToAccount}
          </Button>
          <Button fullWidth onClick={close} variant="link" data-test="close-button">
            {lang.close}
          </Button>
        </div>
      </ModalContent>
    </ModalContainer>
  )
}

AccountImportErrorModal.propTypes = {
  /** Callback when the go to account button is clicked */
  goToAccount: PropTypes.func.isRequired,
  /** Callback for closing the modal */
  close: PropTypes.func.isRequired,
}

export default memo(AccountImportErrorModal)
