import React, { memo } from 'react'
import {
  GridContainer,
  GridItem,
  Input,
  FormControl,
  FormHelpText,
  FormLabel,
  Toggle,
} from '@instacart/cocktail'
import { config as configService } from '@instacart/enterprise-services'
import { SectionTitle } from '@instacart/enterprise-ui-common'

import { UPDATE_FIELD } from '../constants'
import { useFormDataContext } from '../utils/formDataContext'

const OtherInformation = () => {
  // TODO: Subscribe to config store rather than using the selector directly.
  // Requires buiilding a redux store subscribe hook for easy consumption.
  const config = configService.selectors.getConfig()
  const lang = configService.selectors.getLang()

  const [{ otherInformation }, dispatch] = useFormDataContext()

  // Dispatch input field updates
  const handleUpdate = event => {
    dispatch({
      type: UPDATE_FIELD,
      section: 'otherInformation',
      subsection: 'extData',
      field: event.target.name,
      data: event.target.type === 'checkbox' ? event.target.checked : event.target.value,
    })
  }

  return (
    <div data-test="other-information">
      <SectionTitle text={lang.otherInformation} />
      <GridContainer align="baseline" spacing="xxxSmall">
        {config.customProfileFields.map(field => (
          <React.Fragment key={field.key}>
            <GridItem xSmall={12} small={2}>
              <FormLabel htmlFor={field.key} required={field.required}>
                {field.label}
              </FormLabel>
            </GridItem>
            <GridItem xSmall={12} small={10}>
              <FormControl
                required={field.required}
                error={field.required && !otherInformation.extData[field.key]}
              >
                {field.type === 'checkbox' ? (
                  <Toggle
                    data-test={`custom-field-${field.key}`}
                    id={field.key}
                    name={field.key}
                    onChange={handleUpdate}
                    required={field.required}
                    checked={!!otherInformation.extData[field.key]}
                  />
                ) : (
                  <Input
                    data-test={`custom-field-${field.key}`}
                    id={field.key}
                    name={field.key}
                    fullWidth
                    size="small"
                    onChange={handleUpdate}
                    required={field.required}
                    placeholder={field.label}
                    type={field.type || 'text'}
                    pattern={field.pattern || '.*'}
                    value={otherInformation.extData[field.key] || ''}
                    maxLength={field.maxLength || null}
                  />
                )}
              </FormControl>
              {field.helpText && <FormHelpText>{field.helpText}</FormHelpText>}
            </GridItem>
          </React.Fragment>
        ))}
      </GridContainer>
    </div>
  )
}

OtherInformation.propTypes = {}

export default memo(OtherInformation)
