import React, { useCallback, useEffect, useReducer, useState, useRef } from 'react'
import PropTypes from 'prop-types'
import { useUserProfileQuery, useUserProfileMutation } from '@instacart/enterprise-services-hooks'
import { useLang } from '@app/services/config/hooks'
import { analytics } from '@instacart/enterprise-services'

import * as constants from './constants'
import AccountProfile from './components/AccountProfile'
import reducer, { buildState, exportState } from './reducer'
import { FormDataContext } from './utils/formDataContext'
import useLoyaltyCards from './utils/useLoyaltyCards'

const App = ({ disconnectSocial, importIcAccount }) => {
  // Declare the data we need
  const { data: user } = useUserProfileQuery()
  const [profileMutation, profileMutationState] = useUserProfileMutation()
  const { getPrimaryLoyaltyCard } = useLoyaltyCards()
  const lang = useLang()

  // Setup some local state for showing mutation results to the user
  const [alert, setAlert] = useState(null)

  // Setup some local variables for convenience
  const loyaltyCard = getPrimaryLoyaltyCard()
  const isSaving = profileMutationState.isLoading

  // Setup our local state used for managing the form data prior to submit
  const [state, dispatch] = useReducer(reducer, buildState({ user, loyaltyCard }))

  // Reference to input DOM node for error focus management
  const inputRefs = {
    contactInfo: {
      emailRef: useRef(null),
    },
    personalInformation: {
      birthdayRef: useRef(null),
    },
    contactAddress: {
      postalCodeRef: useRef(null),
    },
  }

  // Reset the state when the data changes
  useEffect(
    () =>
      dispatch({
        type: constants.RESET,
        data: { user, loyaltyCard },
      }),
    [loyaltyCard, user]
  )

  // Update the alert states when mutation results change
  useEffect(() => {
    if (profileMutationState.error) {
      let errorMessage = lang.profileValidation.error
      switch (profileMutationState.error.dev_message) {
        case 'This is not a valid email address.':
          errorMessage = lang.profileValidation.emailInvalid
          inputRefs.contactInfo.emailRef.current.focus()
          break
        case 'Invalid birthday.':
          errorMessage = lang.profileValidation.birthdayInvalid
          inputRefs.personalInformation.birthdayRef.current.focus()
          break
        case 'Error thrown from external service': {
          const { message } = profileMutationState.error
          if (message) {
            errorMessage = message
          }
          break
        }
        case 'This email is already registered. Please login or reset your password.': {
          errorMessage = lang.profileValidation.emailAlreadyRegistered
          inputRefs.contactInfo.emailRef.current.focus()
          break
        }
        case 'This email is already registered.': {
          errorMessage = lang.profileValidation.emailAlreadyRegistered
          inputRefs.contactInfo.emailRef.current.focus()
          break
        }
        case 'Zip code is invalid.': {
          errorMessage = profileMutationState.error.dev_message
          inputRefs.contactAddress.postalCodeRef.current.focus()
          break
        }
        case 'Your birthday cannot be in the future.': {
          errorMessage = profileMutationState.error.dev_message
          inputRefs.personalInformation.birthdayRef.current.focus()
          break
        }
        default:
          break
      }

      setAlert({
        type: 'error',
        message: errorMessage,
      })
    } else if (profileMutationState.data) {
      setAlert({
        type: 'success',
        message: 'Updated successfully!',
      })
    }
  }, [
    inputRefs.contactInfo.emailRef,
    inputRefs.personalInformation.birthdayRef,
    inputRefs.contactAddress.postalCodeRef,
    lang.profileValidation.birthdayInvalid,
    lang.profileValidation.emailInvalid,
    lang.profileValidation.error,
    lang.profileValidation.emailAlreadyRegistered,
    profileMutationState.data,
    profileMutationState.error,
  ])

  // Form submit handler
  const onSubmit = useCallback(
    event => {
      event.preventDefault()
      profileMutation(exportState(state))
      analytics.log({
        event: 'event',
        data: { category: 'account-profile', action: 'submit' },
      })
    },
    [profileMutation, state]
  )

  const onAlertClose = useCallback(() => setAlert(null), [])

  return (
    <FormDataContext.Provider value={[{ ...state, inputRefs }, dispatch]}>
      <AccountProfile
        isSaving={isSaving}
        onSubmit={onSubmit}
        alert={alert}
        onAlertClose={onAlertClose}
        disconnectSocial={disconnectSocial}
        importIcAccount={importIcAccount}
      />
    </FormDataContext.Provider>
  )
}

App.propTypes = {
  disconnectSocial: PropTypes.func.isRequired,
  importIcAccount: PropTypes.func,
}

export default App
