import React, { memo } from 'react'
import PropTypes from 'prop-types'
import {
  utils,
  createStyles,
  Text,
  Button,
  ModalContainer,
  ModalHeader,
  ModalHeaderText,
  ModalContent,
  Alert,
} from '@instacart/cocktail'
import { config as configService } from '@instacart/enterprise-services'

const useStyles = createStyles(
  tokens => ({
    heading: {
      margin: 0,
      [utils.mediaQuery.minWidth(tokens.breakpoint.small)]: {
        fontSize: utils.toPx`${tokens.size.font.h3}`,
      },
    },
    footer: {
      marginTop: tokens.size.spacing.large,
    },
  }),
  { displayName: 'AccountImportSuccessModal' }
)

const AccountImportSuccessModal = ({ close }) => {
  const styles = useStyles()
  const lang = configService.selectors.getLang()

  return (
    <ModalContainer>
      <ModalHeader>
        <ModalHeaderText>
          <Text css={styles.heading} component="h1" variant="large" color="inverse">
            {lang.icImportAccount.title}
          </Text>
        </ModalHeaderText>
      </ModalHeader>
      <ModalContent layout="dialog">
        <Alert variant="success">{lang.icImportAccount.success.inProgress}</Alert>
        <Text
          component="p"
          variant="body"
          dangerouslySetInnerHTML={{
            __html: `${lang.icImportAccount.success.pleaseWait} ${lang.icImportAccount.success.seeAccount}`,
          }}
        />
        <div css={styles.footer}>
          <Button fullWidth onClick={close} data-test="start-shopping-button">
            {lang.startShopping}
          </Button>
        </div>
      </ModalContent>
    </ModalContainer>
  )
}

AccountImportSuccessModal.propTypes = {
  /** Callback to close modal */
  close: PropTypes.func.isRequired,
}

export default memo(AccountImportSuccessModal)
