import React, { memo } from 'react'
import PropTypes from 'prop-types'
import { Button, GridContainer, GridItem, Text } from '@instacart/cocktail'
import { config as configService } from '@instacart/enterprise-services'
import { SectionTitle } from '@instacart/enterprise-ui-common'

import { useFormDataContext } from '../utils/formDataContext'

const ConnectedSocialAccounts = ({ disconnectSocial }) => {
  // TODO: Subscribe to config store rather than using the selector directly.
  // Requires buiilding a redux store subscribe hook for easy consumption.
  const lang = configService.selectors.getLang()
  const config = configService.selectors.getConfig()

  const [{ connectedSocialAccounts }] = useFormDataContext()

  // Create a map of providers like so: {
  //   google: 'Google',
  //   instacart: 'Instacart'
  // }
  const socialSignOnProviderLabelsById = config.socialSignOn?.providers?.reduce((acc, provider) => {
    return {
      ...acc,
      [provider.id]: provider.label,
    }
  }, {})

  return (
    <div data-test="social-accounts">
      <SectionTitle text={lang.connectedSocialAccounts} />
      {connectedSocialAccounts.activeAccounts.length ? (
        connectedSocialAccounts.activeAccounts.map(account => (
          <GridContainer
            align="baseline"
            spacing="xxxSmall"
            key={account.signature}
            data-test="social-account"
          >
            <GridItem xSmall={2}>
              <Text>
                {socialSignOnProviderLabelsById && socialSignOnProviderLabelsById[account.provider]}
              </Text>
            </GridItem>
            <GridItem xSmall={5}>
              <Text>{account.social_email}</Text>
            </GridItem>
            <GridItem xSmall={5}>
              <Button onClick={() => disconnectSocial(account)}>{lang.disconnect}</Button>
            </GridItem>
          </GridContainer>
        ))
      ) : (
        <Text>{lang.connectedSocialAccountsNone}</Text>
      )}
    </div>
  )
}

ConnectedSocialAccounts.propTypes = {
  disconnectSocial: PropTypes.func.isRequired,
}

export default memo(ConnectedSocialAccounts)
