/* eslint-disable no-param-reassign */

import produce from 'immer'
import { config as configService, utils } from '@instacart/enterprise-services'
import humps from 'humps'
import * as constants from './constants'

export const getCustomProfileFieldsFromExtData = profileFields => {
  const customProfileKeys = profileFields.map(profileField => profileField.key)
  const customProfileFields = customProfileKeys.reduce((fields, key) => {
    fields.push(...key.split('.'))
    return fields
  }, [])
  return Array.from(new Set(customProfileFields))
}

export const buildState = ({ user, loyaltyCard }) => {
  // TODO: Subscribe to config store rather than using the selector directly.
  // Requires buiilding a redux store subscribe hook for easy consumption.
  const config = configService.selectors.getConfig()

  const phoneNumbers = config.deliveryAddressPhoneNumberFields.reduce((acc, next) => {
    acc[next.key] = user[next.key]
    return acc
  }, {})

  const extData = utils.objectToDotNotationMap(user.ext_data)
  extData.customProfileFields = getCustomProfileFieldsFromExtData(config.customProfileFields)

  return {
    contactInfo: {
      firstName: user.first_name,
      lastName: user.last_name,
      email: user.email,
      primaryNumberKey: user.primary_number_key,
      phoneNumbers,
      loyaltyCardNumber: loyaltyCard ? loyaltyCard.card_number : '',
    },
    personalInformation: {
      birthday: user.birthday,
      gender: user.gender,
    },
    contactAddress: {
      firstName: user.delivery_address.first_name,
      lastName: user.delivery_address.last_name,
      address1: user.delivery_address.address1,
      address2: user.delivery_address.address2,
      address3: user.delivery_address.address3,
      city: user.delivery_address.city,
      province: user.delivery_address.province,
      postalCode: user.delivery_address.postal_code,
    },
    billingAddress: {
      address1: user.billing_address.address1,
      city: user.billing_address.city,
      province: user.billing_address.province,
      postalCode: user.billing_address.postal_code,
    },
    otherInformation: {
      extData,
      icImported: user.ic_imported,
      icUserId: user.ic_user_id,
    },
    connectedSocialAccounts: {
      activeAccounts: user.social_accounts.filter(ac => ac.status),
    },
  }
}

export const exportState = state => {
  const extData = utils.objectFromDotNotationMap(state.otherInformation.extData)

  const exportedState = {
    ...state.contactInfo,
    ...state.contactInfo.phoneNumbers,
    ...state.personalInformation,
    deliveryAddress: {
      ...state.contactAddress,
    },
    billingAddress: {
      ...state.billingAddress,
    },
    extData: {
      ...extData,
    },
  }

  return humps.decamelizeKeys(exportedState, (key, convert) =>
    extData.customProfileFields.includes(key) || key === 'customProfileFields' ? key : convert(key)
  )
}

export default produce((draft, action) => {
  switch (action.type) {
    case constants.UPDATE_FIELD:
      if (action.subsection) {
        draft[action.section][action.subsection][action.field] = action.data
      } else {
        draft[action.section][action.field] = action.data
      }
      return
    case constants.RESET:
      return buildState(action.data)
    default:
      throw new Error('Invalid action')
  }
})
