import React, { memo } from 'react'
import {
  Alert,
  GridContainer,
  GridItem,
  Input,
  FormControl,
  FormLabel,
  Select,
  createStyles,
} from '@instacart/cocktail'
import { config as configService } from '@instacart/enterprise-services'
import { SectionTitle } from '@instacart/enterprise-ui-common'

import { UPDATE_FIELD } from '../constants'
import locations from '../utils/locations'
import { useFormDataContext } from '../utils/formDataContext'

const useStyles = createStyles(tokens => ({
  disabledInput: {
    border: 'none',
    background: 'none',
    color: tokens.color.text.primary,
    padding: '12px 0',
  },
  formLabel: {
    fontStyle: 'italic',
  },
}))

/* eslint-disable complexity */
const ContactAddress = () => {
  // TODO: Subscribe to config store rather than using the selector directly.
  // Requires buiilding a redux store subscribe hook for easy consumption.
  const styles = useStyles()
  const config = configService.selectors.getConfig()
  const lang = configService.selectors.getLang()

  const [{ contactAddress, inputRefs }, dispatch] = useFormDataContext()

  // Dispatch input field updates
  const handleUpdate = event => {
    dispatch({
      type: UPDATE_FIELD,
      section: 'contactAddress',
      field: event.target.name,
      data: event.target.value,
    })
  }

  const locationsOptions = locations[config.provinceOptions].map(item => ({
    label: item.name,
    value: item.value,
  }))

  return (
    <div data-test="contact-address">
      <SectionTitle
        data-test="section-heading"
        text={config.enableMultipleDeliveryAddresses ? lang.contactAddress : lang.deliveryAddress}
      />
      {config.displayDeliveryAddressNames && (
        <GridContainer align="baseline" spacing="xxxSmall">
          <GridItem xSmall={12} small={2}>
            <FormLabel css={styles.formLabel} htmlFor="address-first-name">
              {lang.firstName}
            </FormLabel>
          </GridItem>
          <GridItem xSmall={12} small={10}>
            <FormControl>
              <Input
                id="address-first-name"
                data-test="address-first-name"
                fullWidth
                type="text"
                size="small"
                name="firstName"
                value={contactAddress.firstName}
                onChange={handleUpdate}
                aria-label={`${config.lang.address} ${config.langAria.firstName}`}
                maxLength={config.maxNameLength}
                disabled={config.profileAddress.disabledFields}
                css={config.profileAddress.disabledFields && styles.disabledInput}
              />
            </FormControl>
          </GridItem>
          <GridItem xSmall={12} small={2}>
            <FormLabel css={styles.formLabel} htmlFor="address-last-name">
              {lang.lastName}
            </FormLabel>
          </GridItem>
          <GridItem xSmall={12} small={10}>
            <FormControl>
              <Input
                id="address-last-name"
                data-test="address-last-name"
                fullWidth
                type="text"
                size="small"
                name="lastName"
                value={contactAddress.lastName}
                onChange={handleUpdate}
                aria-label={`${config.lang.address} ${config.langAria.lastName}`}
                maxLength={config.maxNameLength}
                disabled={config.profileAddress.disabledFields}
                css={config.profileAddress.disabledFields && styles.disabledInput}
              />
            </FormControl>
          </GridItem>
        </GridContainer>
      )}

      <GridContainer align="baseline" spacing="xxxSmall">
        <GridItem xSmall={12} small={2}>
          <FormLabel css={styles.formLabel} data-test="address1-label" htmlFor="address1">
            {config.profileAddress.showAllAddressLabels ? lang.addressLine1 : lang.streetAddress}
          </FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={10}>
          <FormControl>
            <Input
              id="address1"
              data-test="address1"
              fullWidth
              type="text"
              size="small"
              name="address1"
              onChange={handleUpdate}
              value={contactAddress.address1 || ''}
              maxLength={config.deliveryAddressFieldsMaxLength}
              disabled={config.profileAddress.disableAddress1xor3}
              css={config.profileAddress.disableAddress1xor3 && styles.disabledInput}
            />
          </FormControl>
        </GridItem>
      </GridContainer>

      {config.profileAddress.showAddress2Field && (
        <GridContainer align="baseline" spacing="xxxSmall">
          <GridItem xSmall={12} small={2}>
            <FormLabel css={styles.formLabel} data-test="address2-label">
              {config.profileAddress.showAllAddressLabels && lang.addressLine2}
            </FormLabel>
          </GridItem>
          <GridItem xSmall={12} small={10}>
            <FormControl>
              <Input
                data-test="address2"
                fullWidth
                type="text"
                size="small"
                name="address2"
                onChange={handleUpdate}
                value={contactAddress.address2 || ''}
                aria-label={config.langAria.secondAddress}
                maxLength={config.deliveryAddressFieldsMaxLength}
                disabled={config.profileAddress.disableAddress2xor3}
                css={config.profileAddress.disableAddress2xor3 && styles.disabledInput}
              />
            </FormControl>
          </GridItem>
        </GridContainer>
      )}

      {config.profileAddress.showAddress3Field && (
        <GridContainer align="baseline" spacing="xxxSmall">
          <GridItem xSmall={12} small={2}>
            <FormLabel css={styles.formLabel} data-test="address3-label">
              {config.profileAddress.showAllAddressLabels && lang.addressLine3}
            </FormLabel>
          </GridItem>
          <GridItem xSmall={12} small={10}>
            <FormControl>
              <Input
                data-test="address3"
                fullWidth
                type="text"
                size="small"
                name="address3"
                onChange={handleUpdate}
                value={contactAddress.address3 || ''}
                aria-label={config.langAria.secondAddress}
                maxLength={config.deliveryAddressFieldsMaxLength}
                disabled={
                  (config.profileAddress.disableAddress1xor3 && contactAddress.address1) ||
                  (config.profileAddress.disableAddress2xor3 && contactAddress.address2)
                }
                css={
                  ((config.profileAddress.disableAddress1xor3 && contactAddress.address1) ||
                    (config.profileAddress.disableAddress2xor3 && contactAddress.address2)) &&
                  styles.disabledInput
                }
              />
            </FormControl>
          </GridItem>
        </GridContainer>
      )}

      <GridContainer align="baseline" spacing="xxxSmall">
        <GridItem xSmall={12} small={2}>
          <FormLabel css={styles.formLabel} htmlFor="city">
            {lang.city}
          </FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={10}>
          <FormControl>
            <Input
              id="city"
              data-test="city"
              fullWidth
              type="text"
              name="city"
              size="small"
              onChange={handleUpdate}
              value={contactAddress.city || ''}
              maxLength={config.formFieldMaxLengths.city}
              disabled={config.profileAddress.disabledFields}
              css={config.profileAddress.disabledFields && styles.disabledInput}
            />
          </FormControl>
        </GridItem>
      </GridContainer>

      <GridContainer align="baseline" spacing="xxxSmall">
        <GridItem xSmall={12} small={2}>
          <FormLabel css={styles.formLabel} htmlFor="province">
            {lang.province}
          </FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={5}>
          <FormControl>
            {config.profileAddress.disabledFields ? (
              <Input
                id="province"
                data-test="province"
                fullWidth
                type="text"
                size="small"
                name="province"
                value={contactAddress.province}
                disabled
                css={styles.disabledInput}
              />
            ) : (
              <Select
                id="province"
                data-test="province"
                fullWidth
                type="text"
                size="small"
                name="province"
                options={locationsOptions}
                onChange={handleUpdate}
                value={contactAddress.province}
                native
              />
            )}
          </FormControl>
        </GridItem>
      </GridContainer>

      <GridContainer align="baseline" spacing="xxxSmall">
        <GridItem xSmall={12} small={2}>
          <FormLabel css={styles.formLabel} htmlFor="postalCode">
            {lang.postalCode}
          </FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={5}>
          <FormControl required={config.requirePostalCodeDuringRegistration}>
            <Input
              id="postalCode"
              data-test="postalCode"
              fullWidth
              type="text"
              size="small"
              name="postalCode"
              ref={inputRefs?.contactAddress?.postalCodeRef}
              onChange={handleUpdate}
              value={contactAddress.postalCode || ''}
              disabled={config.profileAddress.disabledFields}
              css={config.profileAddress.disabledFields && styles.disabledInput}
            />
          </FormControl>
        </GridItem>
      </GridContainer>

      {lang.addressUpdateProfileInfo &&
        config.enableMultipleDeliveryAddresses &&
        !config.profileAddress.disabledFields && (
          <Alert variant="info" data-test="update-profile-alert">
            {lang.addressUpdateProfileInfo}
          </Alert>
        )}
    </div>
  )
}

ContactAddress.propTypes = {}

export default memo(ContactAddress)
