import React, { memo } from 'react'
import { Alert } from '@instacart/cocktail'
import { config as configService } from '@instacart/enterprise-services'

const DeployInformation = () => {
  const config = configService.selectors.getConfig()

  /* eslint-disable react/jsx-no-literals */
  return (
    <Alert variant="warning" data-test="deploy-information">
      <strong>Deploy Information</strong>
      <div>
        <strong>Version:</strong> {config.version}
      </div>
      <div>
        <strong>Date:</strong> {config.timestamp}
      </div>
    </Alert>
  )
  /* eslint-enable react/jsx-no-literals */
}

export default memo(DeployInformation)
