import React, { memo } from 'react'
import { config as configService } from '@instacart/enterprise-services'
import { createStyles, Text } from '@instacart/cocktail'
import InstacartLogo from '../assets/images/ic-logo.svg'

const useStyles = createStyles(
  tokens => ({
    root: {
      padding: tokens.size.spacing.small,
      background: tokens.color.neutral.grey1,
      borderRadius: tokens.border.radius.regular,
    },
    heading: { marginTop: 0 },
    footer: {
      display: 'flex',
      justifyContent: 'flex-end',
    },
  }),
  { displayName: 'AccountLinkedNotice' }
)

const AccountLinkedNotice = props => {
  const styles = useStyles()
  const lang = configService.selectors.getLang()

  return (
    <div css={styles.root} {...props}>
      <Text css={styles.heading} component="h3" variant="body" bold>
        {lang.icImportAccount.linkedTitle}
      </Text>
      <Text component="p" variant="caption">
        {lang.icImportAccount.accountLinked}
      </Text>
      <div css={styles.footer}>
        <img src={InstacartLogo} alt="Instacart Logo" width="86px" />
      </div>
    </div>
  )
}

export default memo(AccountLinkedNotice)
