import React, { memo } from 'react'
import { GridContainer, GridItem, Input, FormControl, FormLabel, Select } from '@instacart/cocktail'
import { config as configService, utils } from '@instacart/enterprise-services'
import { SectionTitle } from '@instacart/enterprise-ui-common'

import { UPDATE_FIELD } from '../constants'
import locations from '../utils/locations'
import { useFormDataContext } from '../utils/formDataContext'

const BillingAddress = () => {
  // TODO: Subscribe to config store rather than using the selector directly.
  // Requires buiilding a redux store subscribe hook for easy consumption.
  const config = configService.selectors.getConfig()
  const lang = configService.selectors.getLang()
  const isMobile = utils.browser.is('mobile') || utils.browser.is('tablet')

  const [{ billingAddress }, dispatch] = useFormDataContext()

  // Dispatch input field updates
  const handleUpdate = event => {
    dispatch({
      type: UPDATE_FIELD,
      section: 'billingAddress',
      field: event.target.name,
      data: event.target.value,
    })
  }

  const handleProvinceUpdate = event => {
    handleUpdate({ target: { name: 'province', value: event.value || event.target.value } })
  }

  const locationsOptions = locations[config.provinceOptions].map(item => ({
    label: item.name,
    value: item.value,
  }))

  return (
    <div data-test="billing-address">
      <SectionTitle text={lang.billingAddress} />
      <GridContainer align="baseline" spacing="xxxSmall">
        <GridItem xSmall={12} small={2}>
          <FormLabel>{lang.streetAddress}</FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={10}>
          <FormControl>
            <Input
              fullWidth
              type="text"
              size="small"
              name="address1"
              value={billingAddress.address1 || ''}
              onChange={handleUpdate}
            />
          </FormControl>
        </GridItem>
      </GridContainer>

      <GridContainer align="baseline" spacing="xxxSmall">
        <GridItem xSmall={12} small={2}>
          <FormLabel>{lang.city}</FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={10}>
          <FormControl>
            <Input
              fullWidth
              type="text"
              name="city"
              size="small"
              value={billingAddress.city || ''}
              onChange={handleUpdate}
            />
          </FormControl>
        </GridItem>
      </GridContainer>

      <GridContainer align="baseline" spacing="xxxSmall">
        <GridItem xSmall={12} small={2}>
          <FormLabel>{lang.province}</FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={10}>
          <FormControl>
            <Select
              fullWidth
              size="small"
              name="province"
              options={locationsOptions}
              onChange={handleProvinceUpdate}
              value={isMobile ? billingAddress.province : undefined}
              defaultValue={
                isMobile
                  ? undefined
                  : locationsOptions.find(option => option.value === billingAddress.province)
              }
              native={isMobile}
            />
          </FormControl>
        </GridItem>
      </GridContainer>

      <GridContainer align="baseline" spacing="xxxSmall">
        <GridItem xSmall={12} small={2}>
          <FormLabel>{lang.postalCode}</FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={5}>
          <FormControl>
            <Input
              fullWidth
              type="text"
              size="small"
              name="postalCode"
              onChange={handleUpdate}
              value={billingAddress.postalCode || ''}
              pattern={config.postalCodePattern}
            />
          </FormControl>
        </GridItem>
      </GridContainer>
    </div>
  )
}

BillingAddress.propTypes = {}

export default memo(BillingAddress)
