import React, { memo } from 'react'
import PropTypes from 'prop-types'

import dayjs from 'dayjs'
import advancedFormat from 'dayjs/plugin/advancedFormat'

import {
  Alert,
  GridContainer,
  GridItem,
  Button,
  Text,
  createStyles,
  usePrevious,
  utils,
} from '@instacart/cocktail'

import { SectionTitle } from '@instacart/enterprise-ui-common'

dayjs.extend(advancedFormat)

const useStyles = createStyles(
  tokens => ({
    root: {
      marginBottom: 40,
    },
    alert: {
      marginBottom: tokens.size.spacing.large,
    },
    subTitle: {
      margin: 0,
    },
    text: {
      margin: utils.toPx`${tokens.size.spacing.xxSmall} 0 ${tokens.size.spacing.small} 0`,
    },
    button: {
      width: '100%',
      [utils.mediaQuery.minWidth(tokens.breakpoint.medium)]: {
        width: 220,
      },
    },
    gridContainer: {
      margin: 0,
      width: '100%',
      border: utils.toPx`${tokens.border.width.regular} solid ${tokens.color.border.light}`,
    },
    gridRow: {
      margin: 0,
      '& + &': {
        borderTop: utils.toPx`${tokens.border.width.regular} solid ${tokens.color.border.light}`,
      },
    },
    footNote: {
      marginTop: tokens.size.spacing.large,
    },
  }),
  { displayName: 'SubscriptionPlan' }
)

const defaultLang = {
  sectionPlanTitle: 'Subscription Plan',
  noPlanTitle: 'No subscription currently',
  noPlanMsg: 'You are currently not signed up for the monthly or annual subscription.',
  addPlan: 'Purchase a Subscription Now',
  subType: 'Subscription Type',
  expDate: 'Expiration Date',
  successAlertTitle: 'Welcome to your subscription!',
  alreadyOwnSubscriptionAlert: 'You already have a subscription',
  footNote: '',
}

const SubscriptionPlan = props => {
  const styles = useStyles(props)
  const { userSubscriptions, subscriptionCodeExists, onAddSubscriptionPlanClick, i18n } = props

  const prevUserSubscriptions = usePrevious(userSubscriptions)
  const lang = { ...defaultLang, ...i18n }

  // display success alert only when a new subscription is added
  const shouldDisplaySuccessAlert =
    prevUserSubscriptions && prevUserSubscriptions.length < userSubscriptions.length

  return (
    <div css={styles.root}>
      <SectionTitle text={lang.sectionPlanTitle} />
      {userSubscriptions.length > 0 && (
        <>
          {subscriptionCodeExists && !shouldDisplaySuccessAlert && (
            <Alert css={styles.alert} variant="warning" data-test="alert-redeem">
              <Text variant="body" bold>
                {lang.alreadyOwnSubscriptionAlert}
              </Text>
            </Alert>
          )}
          {shouldDisplaySuccessAlert && (
            <Alert css={styles.alert} variant="success" data-test="alert-success">
              <Text variant="body" bold>
                {lang.successAlertTitle}
              </Text>
            </Alert>
          )}
          {userSubscriptions.map(subscription => (
            <div key={subscription.id} data-test="subscription-plan-box">
              <GridContainer style={styles.gridContainer}>
                <GridContainer css={styles.gridRow} style={{ margin: styles.gridRow.margin }}>
                  <GridItem xSmall={6} small={3} medium={2}>
                    <Text component="span" variant="body" bold>
                      {lang.subType}
                    </Text>
                  </GridItem>
                  <GridItem xSmall={6} small={9} medium={10}>
                    <Text component="span" variant="body" data-test="subscription-name">
                      {subscription.name}
                    </Text>
                  </GridItem>
                </GridContainer>
                <GridContainer css={styles.gridRow} style={{ margin: styles.gridRow.margin }}>
                  <GridItem xSmall={6} small={3} medium={2}>
                    <Text component="span" variant="body" bold>
                      {lang.expDate}
                    </Text>
                  </GridItem>
                  <GridItem xSmall={6} small={9} medium={10}>
                    <Text component="span" variant="body">
                      {dayjs(subscription.expiry).format('MMMM Do, YYYY')}
                    </Text>
                  </GridItem>
                </GridContainer>
              </GridContainer>
            </div>
          ))}
          <Text
            css={styles.footNote}
            component="p"
            variant="body"
            data-test="foot-note"
            // eslint-disable-next-line react/no-danger
            dangerouslySetInnerHTML={{ __html: lang.footNote }}
          />
        </>
      )}

      {userSubscriptions.length === 0 && (
        <div data-test="no-plan-box">
          <Text css={styles.subTitle} component="p" variant="large" bold>
            {lang.noPlanTitle}
          </Text>
          <Text css={styles.text} component="p" variant="body">
            {lang.noPlanMsg}
          </Text>
          <Button css={styles.button} size="small" onClick={onAddSubscriptionPlanClick}>
            {lang.addPlan}
          </Button>
        </div>
      )}
    </div>
  )
}

SubscriptionPlan.defaultProps = {
  i18n: {},
}

SubscriptionPlan.propTypes = {
  /**
   * i18n language object
   */
  i18n: PropTypes.shape({
    sectionPlanTitle: PropTypes.string,
    noPlanTitle: PropTypes.string,
    noPlanMsg: PropTypes.string,
    addPlan: PropTypes.string,
    subType: PropTypes.string,
    expDate: PropTypes.string,
    successAlertTitle: PropTypes.string,
    footNote: PropTypes.string,
  }),
  /**
   * List of user owned subscriptions
   */
  userSubscriptions: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      user_id: PropTypes.number,
      enterprise_subscription_id: PropTypes.number,
      status: PropTypes.bool,
      created: PropTypes.string,
      expiry: PropTypes.string,
      modified: PropTypes.string,
    })
  ).isRequired,
  /**
   * Whether a subscription code was passed in
   */
  subscriptionCodeExists: PropTypes.bool,
  /**
   * The function that gets called when the 'Add Subscription Plan' button is clicked
   */
  onAddSubscriptionPlanClick: PropTypes.func.isRequired,
}

export default memo(SubscriptionPlan)
