import React from 'react'
import PropTypes from 'prop-types'
import { v4 as uuid } from 'uuid'
import dayjs from 'dayjs'
import { config as configService, lang as langService } from '@instacart/enterprise-services'
import { createStyles, Alert, LoadingDots, Pager, AccordionList } from '@instacart/cocktail'

import usePaginatedTransactionHistory from '../hooks/usePaginatedTransactionHistory'
import TransactionAccordion from './TransactionAccordion'

const DEFAULT_LIMIT = 10

const defaultLang = {
  noTransactions: 'There are currently no transactions to show.',
  goToPage: 'Go to page {{page}}',
  page: 'Page',
  prev: 'Prev',
  next: 'Next',
}

const fieldFormatters = {
  date: date => dayjs(date).format('MMM D, YYYY'),
}

const useStyles = createStyles(
  {
    center: {
      display: 'flex',
      justifyContent: 'center',
    },
  },
  { displayName: 'TransactionAccordions' }
)

const TransactionAccordions = ({ limitPerPage }) => {
  const {
    currentPage,
    setCurrentPage,
    totalCount,
    loyaltyTransactionFields,
    data,
    isLoading,
  } = usePaginatedTransactionHistory(limitPerPage)

  const styles = useStyles()
  const lang = { ...defaultLang, ...configService.selectors.getLang() }

  if (typeof totalCount !== 'number') {
    return <LoadingDots data-test="loading-unknown-total" />
  }

  if (totalCount === 0) {
    return (
      <Alert variant="info" data-test="alert-no-transactions">
        {lang.noTransactions}
      </Alert>
    )
  }

  return (
    <>
      {isLoading ? (
        <div css={styles.center} data-test="loading-known-total">
          <LoadingDots />
        </div>
      ) : (
        <AccordionList>
          {data.transactions.map(transaction => {
            const transactionId = uuid()
            return (
              <TransactionAccordion
                key={transactionId}
                transaction={{ ...transaction, id: transactionId }}
                transactionFields={loyaltyTransactionFields}
                fieldFormatters={fieldFormatters}
              />
            )
          })}
        </AccordionList>
      )}
      <Pager
        currentPage={currentPage}
        pageCount={Math.ceil(totalCount / limitPerPage)}
        onPageClick={page => setCurrentPage(page)}
        ariaLabelLinkBuilder={page => langService.interpolate(lang.goToPage, { page })}
        pageLabel={lang.page}
        previousLinkLabel={lang.prev}
        nextLinkLabel={lang.next}
      />
    </>
  )
}

TransactionAccordions.propTypes = {
  /** Maximum number of transactions to show per page */
  limitPerPage: PropTypes.number,
}

TransactionAccordions.defaultProps = {
  limitPerPage: DEFAULT_LIMIT,
}

export default TransactionAccordions
