import React from 'react'
import PropTypes from 'prop-types'

import { LoadingDots } from '@instacart/cocktail'

import AccountPageContainer from '../../../common/components/AccountPageContainer'
import SubscriptionPlan from '../components/SubscriptionPlan'
import SubscriptionRedeem from '../components/SubscriptionRedeem'

import { ServiceContext } from '../services/ServiceProvider'

class SubscriptionPage extends React.Component {
  static propTypes = {
    /**
     * The callback for when the CTA to add a subscription is invoked
     */
    onAddSubscriptionPlanClick: PropTypes.func,
    /**
     * The callback for when redeem subscription is successful
     */
    onRedeemSubscriptionSuccess: PropTypes.func,
    /**
     * This can be passed in to pre-populate the subscription code input field.
     * This component assumes that when this field is populated, the user is trying to
     * claim a subscription (i.e. after clicking a gifted subscription link)
     */
    initialSubscriptionCode: PropTypes.string,
  }

  static defaultProps = {
    initialSubscriptionCode: '',
  }

  static contextType = ServiceContext

  constructor(props) {
    super(props)
    this.state = {
      hasLoaded: false,
    }
  }

  componentDidMount() {
    const { subscriptions } = this.context
    return subscriptions.promiseActions.getUserSubscriptions().then(() => {
      this.setState({ hasLoaded: true })
    })
  }

  onSubscriptionCodeSubmit = code => {
    const { subscriptions } = this.context
    return subscriptions.promiseActions.redeemSubscription(code)
  }

  render() {
    const { hasLoaded } = this.state
    const {
      onAddSubscriptionPlanClick,
      initialSubscriptionCode,
      onRedeemSubscriptionSuccess,
    } = this.props
    const { config, loading, subscriptions } = this.context

    // Language object from config
    const { lang = {} } = config.selectors.getConfig()
    // All lang items for this component should be namespaced under "subscriptionPage"
    const { subscriptionPage: subscriptionPageLang = {} } = lang

    const userSubscriptions = subscriptions.selectors.getUserSubscriptions()

    const redeemAction = subscriptions.constants.REDEEM_SUBSCRIPTION
    const redeemIsLoading = loading.selectors.isLoading(redeemAction)

    return (
      <AccountPageContainer>
        {!hasLoaded ? (
          <LoadingDots />
        ) : (
          <>
            <SubscriptionPlan
              onAddSubscriptionPlanClick={onAddSubscriptionPlanClick}
              userSubscriptions={userSubscriptions}
              i18n={subscriptionPageLang}
              subscriptionCodeExists={initialSubscriptionCode.length > 0}
            />
            {userSubscriptions.length === 0 && (
              <SubscriptionRedeem
                initialSubscriptionCode={initialSubscriptionCode}
                onSubscriptionCodeSubmit={this.onSubscriptionCodeSubmit}
                onRedeemSubscriptionSuccess={onRedeemSubscriptionSuccess}
                loading={redeemIsLoading}
                i18n={subscriptionPageLang}
              />
            )}
          </>
        )}
      </AccountPageContainer>
    )
  }
}

export default SubscriptionPage
