import { useState, useEffect } from 'react'
import { config as configService } from '@instacart/enterprise-services'
import { useLoyaltyTransactionsQuery } from '@instacart/enterprise-services-hooks'

const usePaginatedTransactionHistory = limitPerPage => {
  const formatQueryString = page => `limit=${limitPerPage}&offset=${limitPerPage * (page - 1)}`

  const [currentPage, setCurrentPage] = useState(1)
  const [totalCount, setTotalCount] = useState()

  const { loyaltyTransactionFields } = configService.selectors.getConfig()
  const { data, isLoading, error } = useLoyaltyTransactionsQuery(formatQueryString(currentPage))

  if (error) {
    throw error
  }

  useEffect(() => {
    if (typeof data?.total_count !== 'undefined' && totalCount !== data.total_count) {
      setTotalCount(data.total_count)
    }
  }, [data, totalCount])

  return {
    currentPage,
    setCurrentPage,
    totalCount,
    loyaltyTransactionFields,
    data,
    isLoading,
  }
}

export default usePaginatedTransactionHistory
