import React, { memo } from 'react'
import {
  createStyles,
  GridContainer,
  GridItem,
  Input,
  FormControl,
  FormLabel,
  FormHelpText,
  Select,
} from '@instacart/cocktail'
import { useConfig, useLang } from '@app/services/config/hooks'
import { SectionTitle } from '@instacart/enterprise-ui-common'

import { UPDATE_FIELD } from '../constants'
import { useFormDataContext } from '../utils/formDataContext'

const useStyles = createStyles(tokens => ({
  fieldset: {
    border: 0,
    padding: 0,
    margin: 0,
    width: '100%',
    marginBottom: tokens.size.layout.xSmall,
    '> legend': {
      marginBottom: tokens.size.layout.xSmall,
      padding: 0,
    },
  },
  disabledInput: {
    border: 'none',
    background: 'none',
    color: tokens.color.text.primary,
    padding: '12px 0',
  },
  formLabel: {
    fontStyle: 'italic',
  },
  radio: {
    verticalAlign: 'middle',
    marginLeft: tokens.size.spacing.xxSmall,
  },
  requiredFieldsMessage: {
    fontStyle: 'italic',
    margin: '0',
    textAlign: 'left',
    color: tokens.color.text.secondary,
  },
  actions: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    margin: '24px 0 16px',
  },
  editLink: {
    fontSize: '16px',
    color: tokens.color.brand.primary.dark,
  },
}))

const ContactInformation = () => {
  const styles = useStyles()
  const config = useConfig()
  const lang = useLang()

  const [{ contactInfo, inputRefs }, dispatch] = useFormDataContext()

  // Dispatch input field updates
  const handleUpdate = (event, subsection) => {
    dispatch({
      type: UPDATE_FIELD,
      section: 'contactInfo',
      subsection,
      field: event.target.name,
      data: event.target.value,
    })
  }

  // Handle regular input field updates
  const handleUpdateField = event => handleUpdate(event)

  // Handle phone number input field updates
  const handleUpdatePhoneNumber = event => handleUpdate(event, 'phoneNumbers')

  const preferredNumberOptions = config.deliveryAddressPhoneNumberFields.map(field => ({
    label: field.label,
    value: field.key,
  }))

  const hasMultiplePhoneNumbers = preferredNumberOptions.length > 1

  const firstNameInvalidPattern =
    contactInfo.firstName && !contactInfo.firstName.match(new RegExp(config.namePattern))
  const lastNameInvalidPattern =
    contactInfo.lastName && !contactInfo.lastName.match(new RegExp(config.namePattern))

  return (
    <div data-test="contact-information">
      <div css={styles.actions}>
        <p css={styles.requiredFieldsMessage}>{lang.requiredFieldsMessage}</p>
        {config.profile.showEditLink && (
          <a css={styles.editLink} href={config.profile.editLinkUrl}>
            {config.profile.editLinkText}
          </a>
        )}
      </div>
      <SectionTitle text="Contact Information" />
      <GridContainer align="baseline" spacing="xxxSmall">
        <GridItem xSmall={12} small={2}>
          <FormLabel css={styles.formLabel} htmlFor="first-name" required>
            {lang.firstName}
          </FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={10}>
          <FormControl required error={!contactInfo.firstName || firstNameInvalidPattern}>
            <Input
              data-test="first-name"
              fullWidth
              id="first-name"
              type="text"
              size="small"
              name="firstName"
              value={contactInfo.firstName || ''}
              placeholder={lang.firstName}
              onChange={handleUpdateField}
              maxLength={config.maxNameLength}
              pattern={config.namePattern}
              disabled={config.profile.disabledNameFields}
              css={config.profile.disabledNameFields && styles.disabledInput}
              aria-label={`${config.lang.profile} ${config.langAria.contactInformation} ${config.langAria.firstName} ${config.langAria.required}`}
              aria-describedby="first-name-help"
            />
            {!contactInfo.firstName && (
              <FormHelpText id="first-name-help" data-test="first-name-help">
                {lang.requiredForm.firstName}
              </FormHelpText>
            )}
            {firstNameInvalidPattern && (
              <FormHelpText id="first-name-help" data-test="first-name-help">
                {lang.requiredForm.firstNamePattern}
              </FormHelpText>
            )}
          </FormControl>
        </GridItem>
        <GridItem xSmall={12} small={2}>
          <FormLabel css={styles.formLabel} htmlFor="last-name" required>
            {lang.lastName}
          </FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={10}>
          <FormControl required error={!contactInfo.lastName || lastNameInvalidPattern}>
            <Input
              data-test="last-name"
              id="last-name"
              fullWidth
              type="text"
              size="small"
              name="lastName"
              value={contactInfo.lastName || ''}
              placeholder={lang.lastName}
              onChange={handleUpdateField}
              maxLength={config.maxNameLength}
              pattern={config.namePattern}
              readOnly={config.disableInput.last_name}
              disabled={config.profile.disabledNameFields}
              css={config.profile.disabledNameFields && styles.disabledInput}
              aria-label={`${config.lang.profile} ${config.langAria.contactInformation} ${config.langAria.lastName} ${config.langAria.required}`}
              aria-describedby="last-name-help"
            />
            {!contactInfo.lastName && (
              <FormHelpText id="last-name-help" data-test="last-name-help">
                {lang.requiredForm.lastName}
              </FormHelpText>
            )}
            {lastNameInvalidPattern && (
              <FormHelpText id="last-name-help" data-test="last-name-help">
                {lang.requiredForm.lastNamePattern}
              </FormHelpText>
            )}
          </FormControl>
        </GridItem>

        <GridItem xSmall={12} small={2}>
          <FormLabel css={styles.formLabel} htmlFor="email" required>
            {lang.email}
          </FormLabel>
        </GridItem>
        <GridItem xSmall={12} small={10}>
          <FormControl
            required
            error={!contactInfo.email || contactInfo.email?.length > config.maxEmailLength}
          >
            <Input
              data-test="email"
              fullWidth
              id="email"
              ref={inputRefs?.contactInfo?.emailRef}
              type="email"
              name="email"
              size="small"
              value={contactInfo.email || ''}
              placeholder={lang.email}
              maxLength={config.maxEmailLength}
              onChange={handleUpdateField}
              disabled={config.profile.disabledEmailField}
              css={config.profile.disabledEmailField && styles.disabledInput}
              aria-label={`${config.lang.profile} ${config.langAria.contactInformation} ${config.langAria.email} ${config.langAria.required}`}
              aria-describedby="email-help"
            />
            {!contactInfo.email && (
              <FormHelpText id="email-help" data-test="email-help">
                {lang.requiredForm.email}
              </FormHelpText>
            )}
            {contactInfo.email?.length > config.maxEmailLength && (
              <FormHelpText id="email-help" data-test="email-help">
                {lang.requiredForm.emailMaxLength}
              </FormHelpText>
            )}
          </FormControl>
        </GridItem>

        {config.deliveryAddressPhoneNumberFields.length && (
          <GridItem xSmall={12}>
            <fieldset css={styles.fieldset}>
              {hasMultiplePhoneNumbers && <legend>{lang.phoneNumbers}</legend>}
              {config.deliveryAddressPhoneNumberFields.map(field => (
                <GridContainer key={field.key} direction="row" align="baseline" spacing="xxxSmall">
                  {config.profileSMSOptInMsgOne && field.key === 'mobile_phone_number' && (
                    <GridItem medium={12}>
                      <div
                        data-test="sms-opt-one"
                        className="sms-opt-in-message"
                        // eslint-disable-next-line react/no-danger
                        dangerouslySetInnerHTML={{ __html: config.profileSMSOptInMsgOne }}
                      />
                    </GridItem>
                  )}
                  <GridItem xSmall={12} small={2}>
                    <FormLabel
                      css={styles.formLabel}
                      htmlFor={`phone-number-${field.key}`}
                      required={
                        contactInfo.primaryNumberKey === field.key &&
                        config.collectPhoneNumbersDuringRegistration
                      }
                    >
                      {field.label}
                    </FormLabel>
                  </GridItem>
                  <GridItem xSmall={12} small={10}>
                    <FormControl
                      required={
                        contactInfo.primaryNumberKey === field.key &&
                        config.collectPhoneNumbersDuringRegistration
                      }
                      error={
                        (contactInfo.primaryNumberKey === field.key &&
                          config.collectPhoneNumbersDuringRegistration &&
                          !contactInfo.phoneNumbers[field.key]) ||
                        contactInfo.phoneNumbers[field.key]?.length > config.phoneNumberMaxLength
                      }
                    >
                      <Input
                        data-test={`phone-number-${field.key}`}
                        fullWidth
                        type="tel"
                        size="small"
                        id={`phone-number-${field.key}`}
                        name={field.key}
                        maxLength={config.phoneNumberMaxLength}
                        onChange={handleUpdatePhoneNumber}
                        pattern={config.phoneNumberPattern}
                        required={
                          contactInfo.primaryNumberKey === field.key &&
                          config.collectPhoneNumbersDuringRegistration
                        }
                        value={contactInfo.phoneNumbers[field.key] || ''}
                        placeholder={field.label}
                        aria-label={`${config.lang.profile} ${config.langAria.contactInformation} ${
                          field.label
                        } ${
                          contactInfo.primaryNumberKey === field.key &&
                          config.collectPhoneNumbersDuringRegistration
                            ? config.langAria.required
                            : ''
                        }`}
                        aria-describedby="phone-number-help"
                        disabled={field.disabled}
                        css={field.disabled && styles.disabledInput}
                      />
                      {((contactInfo.primaryNumberKey === field.key &&
                        !contactInfo.phoneNumbers[field.key] &&
                        config.collectPhoneNumbersDuringRegistration) ||
                        contactInfo.phoneNumbers[field.key]?.length >
                          config.phoneNumberMaxLength) && (
                        <FormHelpText id="phone-number-help" data-test="phone-number-help">
                          {lang.requiredForm.phone}
                        </FormHelpText>
                      )}
                    </FormControl>
                  </GridItem>
                  {config.profileSMSOptInMsgTwo && field.key === 'mobile_phone_number' && (
                    <GridItem medium={12}>
                      <div
                        data-test="sms-opt-two"
                        className="sms-opt-in-message"
                        // eslint-disable-next-line react/no-danger
                        dangerouslySetInnerHTML={{ __html: config.profileSMSOptInMsgTwo }}
                      />
                    </GridItem>
                  )}
                </GridContainer>
              ))}
            </fieldset>
            {hasMultiplePhoneNumbers && (
              <GridContainer align="baseline" spacing="xxxSmall">
                <GridItem xSmall={12} small={2}>
                  <FormLabel css={styles.formLabel} htmlFor="primaryNumberKey">
                    {lang.preferredPhoneNumber}
                  </FormLabel>
                </GridItem>
                <GridItem xSmall={12} small={10}>
                  <FormControl>
                    <Select
                      id="primaryNumberKey"
                      data-test="primaryNumberKey"
                      fullWidth
                      type="text"
                      size="small"
                      name="primaryNumberKey"
                      options={preferredNumberOptions}
                      onChange={handleUpdateField}
                      value={contactInfo.primaryNumberKey}
                      native
                    />
                  </FormControl>
                </GridItem>
              </GridContainer>
            )}
          </GridItem>
        )}

        {config.profile.showLoyaltyReadOnly && (
          <>
            <GridItem xSmall={12} small={2}>
              <FormLabel css={styles.formLabel} htmlFor="loyaltyCard">
                {lang.loyaltyCard}
              </FormLabel>
            </GridItem>
            <GridItem xSmall={12} small={10}>
              <FormControl required>
                <Input
                  data-test="loyaltyCard"
                  disabled
                  css={styles.disabledInput}
                  fullWidth
                  type="text"
                  size="small"
                  id="loyaltyCard"
                  value={contactInfo.loyaltyCardNumber || ''}
                  aria-label={config.langAria.loyaltyCard}
                />
              </FormControl>
            </GridItem>
          </>
        )}
      </GridContainer>
    </div>
  )
}

ContactInformation.propTypes = {}

export default memo(ContactInformation)
