import React, { memo } from 'react'
import PropTypes from 'prop-types'
import { TableRow, TableCell } from '@instacart/cocktail'

const TransactionRow = ({ transaction, index, transactionFields, fieldFormatters }) => (
  <TableRow>
    {transactionFields.map(field => {
      const value = transaction[field.name]

      return (
        <TableCell
          key={`${transaction.id}-${field.name}`}
          data-test={`transaction-${index}-${field.name}`}
        >
          {(field.type && fieldFormatters[field.type](value)) || value || '-'}
        </TableCell>
      )
    })}
  </TableRow>
)

TransactionRow.propTypes = {
  /**
   * Transaction object, must include id
   */
  transaction: PropTypes.shape({ id: PropTypes.string.isRequired }).isRequired,
  index: PropTypes.number.isRequired,
  /** Array of all fields the retailer has configured to show for a transaction */
  transactionFields: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      type: PropTypes.string,
    })
  ).isRequired,
  /**
   * Formatting/mutator functions to apply to the transaction field values
   * Should match with the type prop on the transactionFields object
   * */
  // eslint-disable-next-line react/forbid-prop-types
  fieldFormatters: PropTypes.object,
}

TransactionRow.defaultValues = {
  fieldFormatters: {},
}

export default memo(TransactionRow)
