import React, { memo } from 'react'
import PropTypes from 'prop-types'
import { createStyles, utils } from '@instacart/cocktail'

const useStyles = createStyles(
  // TBD
  tokens => ({
    root: {
      padding: utils.toPx`0 ${tokens.size.spacing.small} ${tokens.size.spacing.small} ${tokens.size.spacing.small}`,
      [utils.mediaQuery.minWidth(tokens.breakpoint.small)]: {
        padding: 0,
      },
    },
  }),
  { displayName: 'AccountPageContainer' }
)

const AccountPageContainer = props => {
  const { children } = props
  const styles = useStyles(props)

  return <div css={styles.root}>{children}</div>
}

AccountPageContainer.propTypes = {
  children: PropTypes.node.isRequired,
}

export default memo(AccountPageContainer)
