import React, { Suspense } from 'react'
import PropTypes from 'prop-types'
import { ErrorBoundary } from '@instacart/enterprise-ui-common'
import { LoadingDots, Alert, createStyles } from '@instacart/cocktail'
import { useUserProfileQuery } from '@instacart/enterprise-services-hooks'
import App from './App'

const useStyles = createStyles(tokens => ({
  loading: {
    padding: tokens.size.spacing.xSmall,
    textAlign: 'center',
  },
}))

const Wrapper = ({ disconnectSocial, importIcAccount }) => {
  const styles = useStyles()
  const { data: user } = useUserProfileQuery()

  return (
    <ErrorBoundary
      fallback={
        <Alert variant="error">
          {
            // eslint-disable-next-line react/jsx-no-literals
          }
          Something went wrong
        </Alert>
      }
    >
      <Suspense
        fallback={
          <div css={styles.loading}>
            <LoadingDots />
          </div>
        }
      >
        {user ? (
          <App disconnectSocial={disconnectSocial} importIcAccount={importIcAccount} />
        ) : (
          <div css={styles.loading}>
            <LoadingDots data-test="profile-loading" />
          </div>
        )}
      </Suspense>
    </ErrorBoundary>
  )
}

Wrapper.propTypes = {
  disconnectSocial: PropTypes.func.isRequired,
  importIcAccount: PropTypes.func,
}

export default Wrapper
