import React, { memo, useState } from 'react'
import PropTypes from 'prop-types'
import { config as configService } from '@instacart/enterprise-services'
import {
  utils,
  createStyles,
  useMediaQuery,
  Text,
  Button,
  ButtonGroup,
  Checkbox,
  ModalContainer,
  ModalHeader,
  ModalHeaderText,
  ModalHeaderAdornment,
  ModalXButton,
  ModalContainerAdornment,
  ModalContent,
} from '@instacart/cocktail'
import InstacartLogo from '../assets/images/ic-logo.svg'

const useStyles = createStyles(
  tokens => ({
    heading: {
      margin: 0,
      [utils.mediaQuery.minWidth(tokens.breakpoint.small)]: {
        fontSize: utils.toPx`${tokens.size.font.h3}`,
      },
    },
    logo: {
      textAlign: 'center',
    },
    features: {
      background: tokens.color.neutral.grey1,
      padding: utils.toPx`${tokens.size.spacing.small} ${tokens.size.spacing.small} ${tokens.size.spacing.small} 0`,
      overflow: 'auto',
    },
    list: {
      margin: 0,
    },
    footer: {
      flexFlow: 'column nowrap',
      marginTop: tokens.size.spacing.large,
      [utils.mediaQuery.minWidth(tokens.breakpoint.small)]: {
        flexFlow: 'row nowrap',
        justifyContent: 'center',
      },
    },
    dontAskMeAgain: {
      padding: utils.toPx`${tokens.size.spacing.medium} 0`,
    },
  }),
  { displayName: 'AccountImportModal' }
)

const AccountImportModal = ({ onImportClick, cancel }) => {
  const styles = useStyles()
  const desktop = useMediaQuery(tokens => utils.mediaQuery.minWidth(tokens.breakpoint.small))
  const lang = configService.selectors.getLang()
  const [icImportOptOut, setIcImportOptOut] = useState(false)
  const dismissModalParams = { skippedButtonClicked: false, importOptOut: false }

  return (
    <ModalContainer roundedCorners={desktop}>
      {desktop && (
        <ModalContainerAdornment>
          <ModalXButton
            onClick={() => cancel(dismissModalParams)}
            data-test="desktop-modal-close"
          />
        </ModalContainerAdornment>
      )}
      <ModalHeader>
        <ModalHeaderText>
          <Text css={styles.heading} component="h1" variant="large" color="inverse">
            {lang.icImportAccount.title}
          </Text>
        </ModalHeaderText>
        {!desktop && (
          <ModalHeaderAdornment position="right">
            <ModalXButton
              onClick={() => cancel(dismissModalParams)}
              data-test="mobile-modal-close"
            />
          </ModalHeaderAdornment>
        )}
      </ModalHeader>
      <ModalContent layout="medium">
        <div css={styles.logo}>
          <img src={InstacartLogo} alt="Instacart Logo" width="134px" />
        </div>
        <Text component="p" variant="body">
          {lang.icImportAccount.intro}
        </Text>
        <Text component="p" variant="body">
          {lang.icImportAccount.listTitle}
        </Text>
        <div css={styles.features}>
          <ul css={styles.list}>
            {lang.icImportAccount.featureList.map((feature, index) => (
              // eslint-disable-next-line react/no-array-index-key
              <li key={index}>
                <Text variant="body">{feature}</Text>
              </li>
            ))}
          </ul>
        </div>
        <Text component="p" variant="body">
          {lang.icImportAccount.outro}
        </Text>
        <Checkbox
          id="dont-ask-me-again-checkbox"
          data-test="dont-ask-me-again-checkbox"
          inputSize="small"
          onChange={event => setIcImportOptOut(event.target.checked)}
          checked={icImportOptOut}
          styles={styles.dontAskMeAgain}
        >
          {lang.icImportAccount.dontAskMeAgain}
        </Checkbox>
        <ButtonGroup style={styles.footer}>
          <Button
            onClick={() => onImportClick({ importOptOut: icImportOptOut })}
            data-test="import-account-button"
          >
            {lang.icImportAccount.importAccount}
          </Button>
          <Button
            onClick={() => cancel({ skippedButtonClicked: true, importOptOut: icImportOptOut })}
            variant="link"
            data-test="skip-button"
          >
            {lang.skipForNow}
          </Button>
        </ButtonGroup>
      </ModalContent>
    </ModalContainer>
  )
}

AccountImportModal.propTypes = {
  /** Callback for clicking on the import account button */
  onImportClick: PropTypes.func.isRequired,
  /** Callback for closing the modal */
  cancel: PropTypes.func.isRequired,
}

export default memo(AccountImportModal)
