import React from 'react'
import PropTypes from 'prop-types'

export const ServiceContext = React.createContext({})

class ServiceProvider extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      unsubList: [], // List of service `unsubscribe` callbacks to be executed when umounting
      subscribedServices: this.buildServiceObject(), // The object passed to child components
    }
  }

  componentDidMount() {
    const { services } = this.props
    const { unsubList } = this.state

    // Subscribe to each injected service.
    // Update this component state when the service data changes - causing a redraw.
    // Store a list of `unsubscribe` callbacks that we can call on umount.
    services.forEach(service => {
      const unsub = service.store.subscribe(() => {
        this.setState({ subscribedServices: this.buildServiceObject() })
      })

      this.setState({ unsubList: unsubList.concat([unsub]) })
    })
  }

  componentWillUnmount() {
    // Call all the `unsubscribe` callbacks
    this.state.unsubList.forEach(fn => fn())
  }

  buildServiceObject() {
    const { services } = this.props
    return services.reduce((acc, curr) => {
      return {
        ...acc,
        [curr.name]: curr,
      }
    }, {})
  }

  render() {
    return (
      <ServiceContext.Provider value={this.state.subscribedServices}>
        {this.props.children}
      </ServiceContext.Provider>
    )
  }
}

ServiceProvider.propTypes = {
  children: PropTypes.node,
  // eslint-disable-next-line react/forbid-prop-types
  services: PropTypes.array,
}

export default ServiceProvider
